import React, { Component } from 'react';
import './App.css';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {Collapse, AppBar, Toolbar, IconButton, Typography, ThemeProvider, responsiveFontSizes, makeStyles, Grid, Button, Container} from "@material-ui/core";
import About from "./About.js";
import Resume from "./Resume.js";
import Projects from "./Projects.js";
import Experience from "./Experience.js"
import Nav from "./Nav.js"
import { createMuiTheme } from '@material-ui/core/styles'


let theme = createMuiTheme({
  palette: {
    primary: {
      main: '#272C34',
    },
    secondary: {
      main: "#282C34",
    },
    contrastThreshold: 3,
    tonalOffset: 0.2,
  },
});
const useStyles = makeStyles({
  root: {
    background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
    border: 0,
    borderRadius: 3,
    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    color: 'white',
    height: 48,
    padding: '0 30px',
  },
  center: {
      justifyContent: 'center'
  }
});
theme = responsiveFontSizes(theme);

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showHome: true,
      showMain: false
    };
    this.toggleHome = this.toggleHome.bind(this);
  }
  render() {
    return (
        <div>
          <Collapse in={this.state.showMain}>
            <ThemeProvider theme={theme}>
              {this.renderNavBar()}
              <div className="main">
                <a id="about"/>
                {this.renderAbout()}
                <a id="projects"/>
                {this.renderProjects()}
                <a id="experience"/>
                {this.renderExperience()}
                <a id="resume"/>
                {this.renderResume()}
                <a id="contact"/>
                <Typography variant="h6" color="inherit" align="center">Contact me</Typography>
                {this.renderContact()}
              </div>
            </ThemeProvider>
          </Collapse>
            {this.renderHome()}
        </div>


    );
  }
  toggleHome()
  {
      const newState = this.state;
      newState.showHome = !this.state.showHome;
      newState.showMain = !this.state.showMain;
      this.setState({newState});
  }
  renderHome(){
    return (
        <Collapse in={this.state.showHome}>
        <div className="home">
          <div className="home-header">
            <Typography variant="h2">JACK PENHALE</Typography>
            <Typography variant="h5">SOFTWARE DEVELOPER // PHOTOGRAPHER</Typography>
          </div>
          <div className="chevron">
            <ExpandMoreIcon 
              onClick={this.toggleHome}
              className="adc"
              fontSize="inherit"
              color="inherit"
              />
          </div>
        </div>
        </Collapse>

    );
  }
  renderAbout()
  {
      return(
              <About closePage={this.toggleAbout}/>
      );
  }

  renderNavBar() {
    return (
      <Nav
        toggleHome = {this.toggleHome}
        />
    );
  }
  renderContact()
  {
    return (
      <Container 
        className="contact"
        maxWidth="xl">
            <Grid               
              container
              justify="center"
              spacing={3}
              > 
               <Typography variant="subtitle1" color="inherit">
                 Please contact me if you are interested in working together! I am currently looking for opportunities.
               </Typography>
            </Grid>
            <Grid
                          container
                          justify="center"
                          spacing={3}
                          className="contactButton"
                          >
              <Button variant="contained" ><a href="mailto:jackpenhale@gmail.com"/>Contact Me</Button>
            </Grid>
        </Container>
    )
  }
  renderResume()
  {
    return (
      <Resume/>
    )
  }
  renderExperience() {
    return (
      <Experience/>
    );
  }
  renderProjects() {
    return (
      <Projects/>
    )
  }
}


export default App;

