import React, {Component} from 'react';
import {Grid, Container, RadioGroup, Radio, Slide, makeStyles, Typography} from "@material-ui/core"
import './App.css';
import piImage from "./images/pi.png"
import tripImage from "./images/tripApp.png"
import hospitalImage from "./images/hospital.png"
import ProjectSlide from "./ProjectSlide"

export default class About extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showTripApp: true,
            showHospitalApp: false,
            showPiProject: false
        }
        
        this.handleChange = this.handleChange.bind(this);
        this.renderTripApp = this.renderTripApp.bind(this);
    }
    

    render() {
        return (
        <Container 
            className="projects"
            maxWidth="xl"
            >
            <Typography variant="h5" color="inherit"> What I've Built</Typography> 
            <Grid 
              container
              spacing={3}
              >
                <Grid xs={12} className="buttons">
                    {this.renderRadioButtons()}
                </Grid>
                <Grid xs={12}>
                {this.state.showTripApp && 
                    this.renderTripApp()}
                {this.state.showPiProject && 
                    this.renderPiProject()}
                {this.state.showHospitalApp && 
                    this.renderHospitalApp()}
                </Grid>
            </Grid>
        </Container>
        )
    }
    renderRadioButtons() {
        return (
        <div>
            <RadioGroup row defaultValue="tripApp" className="center">
                <Radio
                    value="tripApp"
                    color="secondary"
                    name="reactTripApp"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="piProject"
                    color="secondary"
                    name="PiProject"
                    size="small"
                    onChange={this.handleChange}
                />
                <Radio
                    value="hospitalApp"
                    color="secondary"
                    name="FuelHospital"
                    size="small"
                    onChange={this.handleChange}
                />
        </RadioGroup>
      </div>
        );
    }
    handleChange(event)
    {
        switch(event.target.value) {
            case 'tripApp':
                const newState = this.state;
                newState.showTripApp = true;
                newState.showPiProject = false;
                newState.showHospitalApp = false;
                this.setState({newState});
                break;
            case 'piProject':
                const newerState = this.state;
                newerState.showTripApp = false;
                newerState.showPiProject = true;
                newerState.showHospitalApp = false;
                this.setState({newerState});
                break;
            case 'hospitalApp':
                const newestState = this.state;
                newestState.showTripApp = false;
                newestState.showPiProject = false;
                newestState.showHospitalApp = true;
                this.setState({newestState});
                break;
        }

    }
    renderTripApp()
    {
        return (
            <Slide direction="left" in={this.state.showTripApp}>
                <ProjectSlide
                    picture={tripImage}
                    title="Web Trip Application"
                    description="Created a mobile, single page application and microservices during a semester-long,
                    team-based Agile software development project."
                    languages="Slack, GitHub, ZenHub, TravisCI, Code Climate, JUnit, Jest, Maven, NPM, WebPack,
                    IntelliJ, ReactJS, ReactStrap, Leaflet, JavaScript, HTTP, JSON, JSON Schemas, SVG, KML,
                    GSON, SQL, Java Spark"
                    github=""
                />
            </Slide>
        )
    }
    renderPiProject()
    {
        return (
            <Slide direction="left" in={this.state.showPiProject}>
                <ProjectSlide
                    picture={piImage}
                    title="Pi Playlist Selector"
                    description= "Built and developed a Rapsberry Pi heartrate sensor that takes them BPM of a user's heartrate and plays songs with a similar BPM."
                    languages="Python, GitHub, Raspbian"
                    github=""
                />
            </Slide>
        )
    }
    renderHospitalApp()
    {
        return (
            <Slide direction="left" in={this.state.showHospitalApp}>
                <ProjectSlide
                    picture={hospitalImage}
                    title="Hospital Data Application"
                    description="A web application designed to display hospital DRG data along with filters, comments and a login system."
                    languages="-PHP -SQL"
                    github=""
                />
            </Slide>
        )
    }
}